.header{
    height: 60px;
    /* background-color: midnightblue; */
    /* opacity: 0.7; */
    position: fixed;
    /* background-color: #f53255; */
}
.navbar-container{
    /* background-color: #f53255; */
    text-align: right;
    /* padding-bottom: 100px; */
    padding-left: 50px;
    padding-right: 50px;
    /* border: 1px solid black; */
}

.main-nav{
    display: flex;
    list-style: none;
    font-size: 16px;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    /* border: 1px solid black; */
}
li{
    padding: 20px 0px;
}
a{
    text-decoration: none;
    color:  #102142;
}
.brand{
    font-size: 24px;
    font-weight: bolder;
    cursor: pointer;
    /* margin-right: auto; */
}
.nav-links{
    padding-left: 30px;
    cursor: pointer;
}
.navs{
    /* border: 1px solid black; */
    position: absolute;
    top: 0;
    right: 0;
    margin-right: 50px;
    display: flex;
}
.nav-links:hover{
    color: #102142;
    font-weight: bold;
}
li > .active{
    color: #102142;
    font-weight: bold;
}