*{
    margin: 0;
    padding: 0;
}
.homepage-container{
    background-color: rgb(243, 243, 253);
    position: absolute;
    z-index: -5;
}
.header{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    width: 100%;
}
.banner{
    position: relative;
    z-index: -1;
}
.scroll-btn{
    background-color: #102142;
    width: 50px;
    height: 50px;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 10;
    margin: 50px;
}
.scroll-btn1{
    color: white;
}
