.services-container{
    background-color: rgb(243, 243, 253);
    /* background-color: white; */
    text-align: center;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 50px;
    padding-right: 50px;
    /* border: 1px solid black; */
}
h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 2rem;
    letter-spacing: 5px;
    color: #102142;
    line-height: 16px;
}
.section-name{
    display: inline-block;
    width: 400px;
    /* border: 1px solid black; */
}
.underline{
    width: 40%;
    height: 3px;
    background-color: #f53255;
    display: inline-block;
    line-height: 1px;
    margin-top: -70px;
}
.cards-container{
    margin-top: 50px;
    padding-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    /* border: 1px solid black; */
    grid-gap: 50px;  
    margin: auto 80px;  
}
.cards{
    /* width: 250px; */
    align-items: center;
    /* border: 1px solid black; */
    padding: 10px 20px;
    /* margin-left: 40px;
    margin-right: 40px; */
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.2s ease-out;
    padding: 20px;
    align-items: center;
    box-shadow: -8px -8px 8px 0 rgba(255, 255, 255, 0.4), 8px 8px 8px 0 rgba(0, 0, 0, 0.05);
}
.cards img{
    width: 100px;
    margin: 10px;
}
.cards p{
    color: #4b5c7a;
    font-size: 14px;
    letter-spacing: 1px;
}
.cards h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5rem;
    text-transform: uppercase;
    /* line-height: 2rem; */
    margin: 20px auto;
    color: #102142;
}