.testinomial-container{
    background-image: linear-gradient(to right, #102142, #102142c4), url('../../images/team.jpg');
    background-position: center;
    background-size: cover;
    text-align: center;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 50px;
    padding-right: 50px;
    min-height: 50vh;
    /* border: 1px solid black; */
}
.testinomial{
    display: grid;
    grid-template-columns: 1fr;
    height: 150px;
    background-color: white;
    width: 70%;
    margin: auto 170px;
    margin-top: 100px;
    padding: 20px;
}
.test-image{
    height: 130px;
    margin-top: -60px;
}
.test-image img{
    border-radius: 50%;
    height: 100%;
    border: 3px solid white;
}