.News-events-container{
    background-color: rgb(243, 243, 253);
    /* background-color: white; */
    text-align: center;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 50px;
    padding-right: 50px;
    /* border: 1px solid black; */
}
.news-section-name{
    display: inline-block;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    /* border: 1px solid black; */
}
.news-underline{
    width: 15%;
    height: 3px;
    background-color: #f53255;
    display: inline-block;
    line-height: 1px;
    margin-top: -70px;
}
.news-event{
    /* background-color: white; */
    margin-top: 50px;
    /* display: grid;
    grid-template-columns: 1fr 2fr; */
    /* border: 1px solid black; */
    display: inline-block;
    box-shadow: -8px -8px 8px 0 rgba(255, 255, 255, 0.6), 8px 8px 8px 0 rgba(0, 0, 0, 0.09);
}
.news-event-image-1{
    height: 200px;
    width: 250px;
    background-image: linear-gradient(to right, #f53256a6, #10214298),url('../../images/learn.jpg');
    background-size: cover;
    background-position: center;
    clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);
    float: left;
}
.news-event-image-2{
    /* height: 200px;
    background-image: url('../../images/node.jpg');
    background-size: cover;
    background-position: center; */
    height: 200px;
    width: 250px;
    background-image: linear-gradient(to right, #f53256a6, #10214298),url('../../images/node.jpg');
    background-size: cover;
    background-position: center;
    clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);
    float: left;
}

.news-event-description{
    height: 180px;
    width: 500px;
    /* background-color: #f53255; */
    /* border: 1px solid black; */
    padding: 10px;
    float: left;
}
.news-event-description h3{
    font-family: 'Montserrat', sans-serif;
    color: #102142;
    text-align: left;
    margin: 10px auto;
}
.news-event-description p{
    font-family: 'Montserrat', sans-serif;
    color: #102142;
    /* margin: 10px auto; */
    font-size: 12px;
    text-align: left;
}
.news-button{
    padding: 10px 50px;
    font-family: 'Montserrat', sans-serif;
    margin: 30px auto;
    outline: none;
    border: none;
    color: #102142;
    background-color: #f53255;
    float: left;
}