.banner-container{
    height: 90vh;
    background-color: rgb(243, 243, 253);
    padding-top: 70px;
    display: grid;
    grid-template-columns: 2fr 2fr;
    padding-left: 50px;
    padding-right: 50px;
    /* border: 1px solid black; */

}
.banner-container h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 4rem;
    letter-spacing: 10px;
    padding-bottom: 20px;
    color: #102142;
    line-height: 3.7rem;
}
.description{
    /* border: 1px solid black; */
    height: 350px;
    padding-top: 30%;
}
.description h1 span{
    color: #f53255;
}
.description p{
    color: #4b5c7a;
    font-size: 16px;
    letter-spacing: 1px;
}
.image{
    /* border: 1px solid black; */
    height: 470px;
    float: right;
    padding-top: 70px;
}
.image img{
    height: 100%;
    float: right;
}

button{
    padding: 10px 25px;
    font-family: 'Montserrat', sans-serif;
    margin-right: 20px;
    outline: none;
    border: none;
    color: white;
    cursor: pointer;
}
.b1{
    background-color: #f53255;
}
.b1 .b1-1{
    color: white;
}
.b2{
    background-color: #102142;
}
.b2 .b2-2{
    color: white;
}