.footer-container{
    background-color: #f53255;
    text-align: center;
    padding-top: 140px;
    /* padding-bottom: 100px; */
    padding-left: 50px;
    padding-right: 50px;
    min-height: 70vh;
    /* border: 1px solid black; */
}
.top{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
}
/* .footer-brand{
    border: 1px solid black;
} */
.footer-brand h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 3rem;
    line-height: 3rem;
    letter-spacing: 10px;
    padding-bottom: 20px;
    color: #102142;
    /* border: 1px solid black; */
    border-bottom: 3px white solid;
    text-align: left;
}

.footer-brand h1 span{
    color: white;
}
.footer-brand p{
    color: #102142;
    font-size: 16px;
    letter-spacing: 1px;
    margin-top: 20px;
    text-align: left;
    font-family: 'Montserrat', sans-serif;
}
.footer-links{
    /* border: 1px solid black; */
    padding: 10px 20px;

}
.links{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

}
.footer-links p{
    font-size: 18px;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    color: #102142;
    float: left;
    margin: 20px;
}
.footer-contact{
    /* border: 1px solid black; */
    padding: 10px 20px;
}
.footer-contact h1{
    /* font-family: 'Montserrat', sans-serif;
    font-size: 3rem;
    line-height: 3rem;
    letter-spacing: 10px;
    color: #102142;*/
    /* border: 1px solid black; */
    margin-bottom: 30px;
    text-align: left;
}
.footer-contact input{
    margin: 10px auto;
    width: 90%;
    height: 5%;
    border: none;
    padding: 15px;
}
.footer-contact-btn{
    margin: 10px auto;
    width: 100%;
    height: 15%;
    border: none;
    /* padding: 10px; */
    background-color: #102142;
}
.bottom{
    position: absolute;
    bottom: 0;
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr;
    /* border: 1px solid black; */
    width: 90%;
    font-family: 'Montserrat', sans-serif;
}
.bottom h1{
    font-size: 1rem;
    letter-spacing: 1px;
}
