.courses-container{
    background-color: rgb(243, 243, 253);
    /* background-color: white; */
    text-align: center;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 50px;
    padding-right: 50px;
    min-height: 100vh;
    /* border: 1px solid black; */
}
h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 2rem;
    letter-spacing: 5px;
    color: #102142;
    line-height: 16px;
}
.courses-section-name{
    display: inline-block;
    width: 400px;
    /* border: 1px solid black; */
}
.courses-underline{
    width: 60%;
    height: 3px;
    background-color: #f53255;
    display: inline-block;
    line-height: 1px;
    margin-top: -70px;
}

.course-card-container{
    margin-top: 50px;
    padding-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    /* border: 1px solid black; */
    grid-gap: 50px;  
    margin: auto 80px;  
}
.course-card{
    background-color: #102142;
    height: 400px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.2s ease-out;
    align-items: center;
    box-shadow: -8px -8px 8px 0 rgba(255, 255, 255, 0.9), 8px 8px 16px 0 rgba(48, 48, 48, 0.5);
}
.course-image-1{
    /* background-color: #f53255; */
    background-image: url('../../images/angular.jpg');
    background-size: cover;
    background-position: center;
    height: 180px;
}
.course-image-2{
    background-image: url('../../images/full-stack.jpg');
    background-size: cover;
    background-position: center;
    height: 180px;
}
.course-image-3{
    background-image: url('../../images/seo.jpg');
    background-size: cover;
    background-position: center;
    height: 180px;
}
.course-description{
    padding: 20px;
    /* padding-top: 20px; */
}
.course-description h3{
    font-family: 'Montserrat', sans-serif;
    color: white;
    margin: 20px auto;
    float: left;
}
.course-description p{
    font-family: 'Montserrat', sans-serif;
    color: white;
    /* margin: 10px auto; */
    font-size: 12px;
    text-align: left;

}
.course-description small{
    font-family: 'Montserrat', sans-serif;
    color: white;
    margin: 10px auto;
}
.rating-price{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5px;
    margin-top: 20px;
}
.rating{
    float: left;
}
.rating small, .rating div{
    float: left;
    font-size: 16px;
}
/* .price{
    border: 1px white solid;

} */
.price p{
    font-size: 24px;
    font-weight: bold;
    float: right;
    margin-top: 30px;
    /* position: absolute;
    bottom: 0; */
}
.course-btn{
    padding: 10px 50px;
    font-family: 'Montserrat', sans-serif;
    margin: 30px;
    outline: none;
    border: none;
    color: white;
    background-color: #f53255;
    display: inline-block;
}